export type TourStep = {
  targetId: string;
  title: string;
  content: string;
  position?: 'top' | 'bottom' | 'left' | 'right';
}

// Mapping: Role -> Route -> Steps
export const ROLE_TOURS: Record<number, Record<string, TourStep[]>> = {
  
  // --- CADET (Level 10) ---
  10: {
    '/': [
      { targetId: 'nav-dashboard', title: 'Dashboard Home', content: 'This is your main dashboard. Use the navigation bar to access other pages.', position: 'bottom' },
      { targetId: 'dashboard-demerits-card', title: 'Demerit Balance', content: 'This card shows your current active demerits. Keep this number low!', position: 'bottom' },
      { targetId: 'dashboard-tours-card', title: 'Tour Balance', content: 'This is your calculated punishment balance (Tours). 1 Demerit usually equals 1 Tour once your credits are gone.', position: 'bottom' },
    ],
    '/submit': [
      { targetId: 'submit-report-form', title: 'Submission Form', content: 'Select the cadet you are reporting and the offense type here. Be accurate.', position: 'right' }
    ],
    '/reports/history': [
      { targetId: 'history-table-container', title: 'Report History', content: 'This table logs every report you have received. Click any row to view full details or file an appeal.', position: 'top' }
    ]
  },

  // --- STAFF (Level 50+) ---
  50: {
    '/': [
      { targetId: 'nav-daily', title: 'Green Sheet Access', content: 'Click here to view the Daily Disciplinary Report (Green Sheet).', position: 'bottom' },
      { targetId: 'nav-approval', title: 'Approval Queue', content: 'You have reports waiting for your review. Check this daily.', position: 'bottom' }
    ],
    '/reports/daily': [
      { targetId: 'green-sheet-print-btn', title: 'Print Report', content: 'Generate a clean, printable PDF version of the daily report for the bulletin board.', position: 'left' },
      { targetId: 'green-sheet-tabs', title: 'Tour Sheet Toggle', content: 'Switch between the "Green Sheet" (Daily Summary) and the "Tour Sheet" (Punishment Log) here.', position: 'bottom' }
    ],
    '/reports/pending': [
      { targetId: 'pending-approval-list', title: 'Pending Reports', content: 'These reports require your attention. Click a row to Approve, Reject, or Request Revision.', position: 'top' }
    ],
    '/manage': [
      { targetId: 'roster-controls', title: 'Roster Management', content: 'Use these filters to find cadets by Company, Year, or Name.', position: 'bottom' },
      { targetId: 'roster-table', title: 'Cadet Records', content: 'Click on any cadet to view their full profile, history, and edit their details.', position: 'top' }
    ]
  },

  // --- ADMIN (Level 90+) ---
  90: {
    '/admin': [
      { targetId: 'admin-tab-nav', title: 'Configuration Tabs', content: 'Switch between Role Management, Infraction Catalog, and System Settings.', position: 'bottom' },
      { targetId: 'admin-create-btn', title: 'Create New', content: 'Use this button to add new roles or infraction types to the system.', position: 'left' }
    ]
    // Admins automatically inherit Staff tours for other pages via logic below
  }
}

export const getStepsForPage = (role: number, pathname: string) => {
  // 1. Determine config
  let config = ROLE_TOURS[10]; // Default to Cadet
  if (role >= 90) config = { ...ROLE_TOURS[10], ...ROLE_TOURS[50], ...ROLE_TOURS[90] }; // Admin gets everything
  else if (role >= 50) config = { ...ROLE_TOURS[10], ...ROLE_TOURS[50] }; // Staff gets Cadet + Staff

  // 2. Return steps for current path
  return config[pathname] || [];
}