'use client'

import { useState, useEffect } from 'react'
import { usePathname } from 'next/navigation'
import { completeTutorial } from '@/app/onboarding/actions'
import { getStepsForPage } from './TourConfig'

export default function OnboardingTour({ 
  userRole, 
  isCompleted 
}: { 
  userRole: number, 
  isCompleted: boolean 
}) {
  const pathname = usePathname()
  const [isOpen, setIsOpen] = useState(!isCompleted)
  
  // We track the step index LOCAL to the current page
  const [currentStepIndex, setCurrentStepIndex] = useState(0)
  const [stepPosition, setStepPosition] = useState<{top: number, left: number, width: number, height: number} | null>(null)

  // Reset step index when changing pages
  useEffect(() => {
    setCurrentStepIndex(0)
  }, [pathname])

  // Get steps for THIS page
  const steps = getStepsForPage(userRole, pathname)
  const currentStep = steps[currentStepIndex]

  // Logic to find element
  useEffect(() => {
    if (!isOpen || !currentStep) return

    const updatePosition = () => {
      const element = document.getElementById(currentStep.targetId)
      if (element) {
        const rect = element.getBoundingClientRect()
        setStepPosition({
          top: rect.top + window.scrollY,
          left: rect.left + window.scrollX,
          width: rect.width,
          height: rect.height
        })
        element.scrollIntoView({ behavior: 'smooth', block: 'center' })
      }
    }

    const timer = setTimeout(updatePosition, 500)
    window.addEventListener('resize', updatePosition)
    return () => {
      clearTimeout(timer)
      window.removeEventListener('resize', updatePosition)
    }
  }, [currentStepIndex, isOpen, currentStep, pathname])

  const handleNext = async () => {
    if (currentStepIndex < steps.length - 1) {
      setCurrentStepIndex(prev => prev + 1)
    } else {
      // End of tour for THIS page. 
      setIsOpen(false) 
    }
  }

  const handleDismissGlobal = async () => {
    setIsOpen(false)
    await completeTutorial() // Marks the whole tutorial as done in DB
  }

  // If no steps for this page, render nothing
  if (!isOpen || !currentStep || !stepPosition) return null

  return (
    <div className="fixed inset-0 z-[100] pointer-events-none">
      <div className="absolute inset-0 bg-black/50 transition-all duration-300 ease-in-out" />
      
      <div 
        className="absolute transition-all duration-300 ease-in-out border-2 border-indigo-500 rounded-lg shadow-[0_0_0_9999px_rgba(0,0,0,0.7)]"
        style={{
          top: stepPosition.top - 4,
          left: stepPosition.left - 4,
          width: stepPosition.width + 8,
          height: stepPosition.height + 8,
        }}
      />

      <div 
        className="absolute pointer-events-auto bg-white dark:bg-gray-800 p-6 rounded-xl shadow-2xl border border-gray-200 dark:border-gray-700 w-80 transition-all duration-300"
        style={{
          top: stepPosition.top + stepPosition.height + 20,
          left: Math.max(20, Math.min(window.innerWidth - 340, stepPosition.left + (stepPosition.width / 2) - 160)),
        }}
      >
        <div className="flex justify-between items-start mb-2">
          <span className="text-xs font-bold text-indigo-600 uppercase tracking-wider">
            {currentStep.title}
          </span>
          <button onClick={() => setIsOpen(false)} className="text-gray-400 hover:text-gray-600">
            âœ•
          </button>
        </div>
        
        <p className="text-sm text-gray-600 dark:text-gray-300 mb-6 leading-relaxed">
          {currentStep.content}
        </p>

        <div className="flex justify-between items-center border-t border-gray-100 dark:border-gray-700 pt-4">
          <button 
            onClick={handleDismissGlobal}
            className="text-xs text-gray-400 hover:text-gray-600 underline"
          >
            Don't show again
          </button>
          <div className="flex gap-2">
            <span className="text-xs text-gray-400 self-center mr-2">
                {currentStepIndex + 1} / {steps.length}
            </span>
            <button 
                onClick={handleNext}
                className="px-4 py-2 bg-indigo-600 hover:bg-indigo-700 text-white text-sm font-medium rounded-lg shadow-sm"
            >
                {currentStepIndex === steps.length - 1 ? 'Done' : 'Next'}
            </button>
          </div>
        </div>
        
        <div className="absolute -top-2 left-1/2 -translate-x-1/2 w-4 h-4 bg-white dark:bg-gray-800 border-t border-l border-gray-200 dark:border-gray-700 rotate-45 transform" />
      </div>
    </div>
  )
}