'use server'

import { createClient } from '@/utils/supabase/server'

export async function completeTutorial() {
  const supabase = createClient()
  const { data: { user } } = await supabase.auth.getUser()
  
  if (!user) return
  
  await supabase
    .from('profiles')
    .update({ tutorial_completed: true })
    .eq('id', user.id)
}